import wx from 'weixin-js-sdk'
import request from './request.js'

async function wxshare(data){
	let url = 'http://jx.onapp.top/wxshare/wxConfigInfo';
	url = 'https://o400l85452.imdo.co/wxshare/wxConfigInfo'
	let link = window.btoa(location.href.split('#')[0]);
  await request({
		url:url,
		method:'post',
		data:{url:link},
	}).then((res)=>{
		let wxConfigInfo = res.data.data;
		console.log(wxConfigInfo)
		wx.config({
			debug: false,// ,apialertpclogpc
			appId: wxConfigInfo.appid,         // 
			timestamp: wxConfigInfo.timestamp, // 
			nonceStr: wxConfigInfo.nonceStr,   // 
			signature: wxConfigInfo.signature, // 1
			jsApiList: [
				'updateTimelineShareData',
				'updateAppMessageShareData'
			]
		})

		wx.ready(function () {
			//
			wx.updateTimelineShareData({
				title: data.title,   // 
				link: wxConfigInfo.url+'#'+data.url,     // 
				imgUrl: data.imgUrl,    // 
				success: function () {
					console.log("");
				},
				cancel: function () {
					console.log("");
				}
			});
			//
			wx.updateAppMessageShareData({
				title: data.title,
				desc: data.desc, 
				link: wxConfigInfo.url+'#'+data.url,
				imgUrl: data.imgUrl,
				success: function () {
					console.log("");
				},
				cancel: function () {
					console.log("");
				}
			});
	})


	})
}



export default wxshare